<template>
  <div class="article-page">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>面经后台</el-breadcrumb-item>
      <el-breadcrumb-item>面经管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card shadow="never" border="false">
      <!-- 配置头部 -->
      <template #header>
        <div class="header">
          <span>共 {{ total }} 条记录</span>
          <el-button
            @click="openDrawer('add')"
            icon="el-icon-plus"
            size="small"
            type="primary"
            round
          >
            添加面经
          </el-button>
        </div>
      </template>

      <!-- 内容部分
           el-table 表格
           el-table-column 表格的列  lable：列名   prop 配置一列的值

          我们用的 el-card, el-table-column 这些其实都是 element-ui 封装好的组件
          我们使用组件，组件内部的逻辑不用我们写，需要关注组件怎么使用

          类比于：别人可以封装好一系列函数，函数内部的逻辑不用我写，我只需要关注于函数怎么调用即可
       -->
      <el-table :data="list" style="width: 100%">
        <el-table-column prop="stem" label="标题" width="400">
        </el-table-column>
        <el-table-column prop="creator" label="作者"> </el-table-column>
        <el-table-column prop="likeCount" label="点赞"> </el-table-column>
        <el-table-column prop="views" label="浏览数"> </el-table-column>
        <el-table-column prop="createdAt" label="更新时间" width="200">
        </el-table-column>

        <!-- 底层源码：
           <div v-for="(item,index) in data">
              <slot name="default" :row="item" :$index="index"></slot>
          </div>
       -->
        <el-table-column label="操作">
          <!-- obj中有两个常用属性 => $index下标, row一行的数据对象(遍历时的一个item) -->
          <template #default="obj">
            <!-- {{obj.$index}}  {{obj.row.id}}-->
            <div class="actions">
              <i
                class="el-icon-view"
                @click="openDrawer('preview', obj.row.id)"
              >
              </i>
              <i
                class="el-icon-edit-outline"
                @click="openDrawer('edit', obj.row.id)"
              >
              </i>
              <i class="el-icon-delete" @click="del(obj.row.id)"></i>
            </div>
          </template>
        </el-table-column>
      </el-table>

      <!--
      @size-change            每页条数变化，每页10条 => 每页20条
      @current-change         当前页变化，重新加载数据---
      :current-page="当前页"   绑定当前生效的是第几页,控制高亮---
      :page-sizes="[100, 200, 300, 400]" 可供选择的每页条数
      :page-size="100"        当前生效的每页条数---
      :total="值"             总数量---
      layout                  布局容器，设定有哪些控件展示在页面中
     -->
      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="current"
        :page-size="pageSize"
        layout="prev, pager, next"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!-- drawer 外层放，固定定位
         title 配置标题
         :visible.sync  绑定一个布尔值,控制显示隐藏(.sync修饰符，后面人资讲)
         direction="rtl"   从右侧弹出 从右往左开
         :before-close    处理弹窗关闭事件
    -->
    <el-drawer
      :title="drawerTitle"
      :visible.sync="isShowDrawer"
      direction="rtl"
      :before-close="handleClose"
      size="50%"
    >

      <div v-if="drawerType === 'preview'" class="article-preview">
  <h5>{{ form.stem }}</h5>
  <div v-html="form.content"></div>
</div>

      <el-form v-else ref="form" :model="form" :rules="rules" label-width="80px" >
        <el-form-item label="标题" prop="stem">
          <el-input v-model="form.stem" placeholder="输入面经标题"></el-input>
        </el-form-item>
        <el-form-item label="内容" prop="content">
          <!-- 注意：这个quill-editor不是element的表单元素，不受element的直接控制，blur无效的
                    需要手动注册blur事件，调用form的校验
               -->
             <quill-editor v-model="form.content" @blur="handleBlur"></quill-editor>
           </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submit">确认</el-button>
          <el-button>取消</el-button>
        </el-form-item>
      </el-form>
    </el-drawer>
  </div>
</template>

<script>
import { createArticle, getArticleDetail, getArticleList, removeArticle, updateArticle } from '@/api/article'
// require styles
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'

export default {
  name: 'article-page',
  components: {
    quillEditor
  },
  data () {
    return {
      list: [],
      current: 1, // 当前页
      pageSize: 10, // 每页条数
      total: 0, // 总条数
      isShowDrawer: false, // 默认不显示
      drawerType: '', // 弹框操作类型   默认没有打开弹框，初始值为 'add' add preview edit
      form: {
        stem: '', // 标题
        content: '' // content
      },
      rules: {
        stem: [{ required: true, message: '请输入面经标题', trigger: 'blur' }],
        content: [{ required: true, message: '请输入面经标题', trigger: 'blur' }]
      }
    }
  },
  async created () {
    this.initData()
  },
  methods: {
    // 根据 当前页 和 每页条数，初始化数据
    async initData () {
      const { data } = await getArticleList({
        current: this.current,
        pageSize: this.pageSize
      })
      // console.log(data)
      this.list = data.rows
      this.total = data.total
    },
    async del (id) {
      // console.log(id)
      await removeArticle(id)
      this.$message.success('删除成功！')
      this.initData() // 重新渲染
    },
    handleCurrentChange (val) {
      // console.log(val)
      // 更新当前页
      this.current = val
      // 重新根据当前页，进行初始化数据
      this.initData()
    },
    async openDrawer (type, id) {
      // console.log(type,id)
      // type 的值 add preview edit => 对应(自动)设置标题 => 计算属性
      this.drawerType = type

      this.isShowDrawer = true // 显示弹窗

      // 除了添加， 编辑和预览，都是要回显
      if (type !== 'add') {
        const res = await getArticleDetail(id)
        this.form = {
          ...res.data
        }
      }
    },
    handleClose () {
      // console.log('关闭弹窗')
      // 将表单内容重置
      // 无论是哪种情况, 一律将form手动数据清零
      this.form = { stem: '', content: '' }
      if (this.drawerType !== 'preview') {
        // add edit 调用 resetFields 在此处的作用: 重置校验状态
        this.$refs.form.resetFields()
      }
      this.isShowDrawer = false // 关闭弹窗
    },
    handleBlur () {
      this.$refs.form.validateField('content')
    },
    async submit () {
      try {
        // 1.校验表单
        await this.$refs.form.validate()

        // 如何区分当前是 编辑 还是 添加
        if (this.drawerType === 'add') {
          // 2.发送请求
          await createArticle(this.form)
          // 3.添加提示
          this.$message.success('添加成功')
        }
        if (this.drawerType === 'edit') {
          // 发送的是编辑的请求
          const { id, stem, content } = this.form
          await updateArticle({ id, stem, content })
          this.$message.success('修改成功！')
        }
        // 无论是修改还是添加, 都会回到第一页, 重置页码
        this.current = 1
        // 重新渲染
        this.initData()
        // 关闭弹框
        this.handleClose()
      } catch (e) {
        console.log(e)
      }
    }
  },
  computed: {
    drawerTitle () {
      let title = '默认大标题'
      if (this.drawerType === 'add') title = '添加面经'
      if (this.drawerType === 'preview') title = '面经预览'
      if (this.drawerType === 'edit') title = '修改面经'

      return title
    }
  }
}
</script>

<style lang="scss" scoped>
.el-card {
  margin-top: 25px;
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 16px;
  }
  .actions {
    font-size: 18px;
    display: flex;
    justify-content: space-around;
    color: #666;
    > i:hover {
      color: rgba(114, 124, 245, 1);
      cursor: pointer;
    }
  }
}
.el-pagination {
  margin-top: 20px;
  text-align: center;
}
.el-breadcrumb {
  margin-top: 10px;
}
.el-form {
  padding-right: 40px;
}
.quill-editor {
  ::v-deep .ql-editor {
    height: 300px;
  }
}
.el-rate {
  padding: 10px 0;
}
.article-preview {
  padding: 0 40px 40px 40px;
  > h5 {
    font-size: 20px;
    color: #666;
    border-bottom: 1px dashed #ccc;
    padding-bottom: 30px;
    margin: 0 0 20px 0;
  }
}
</style>
